import "./App.css";
import React, { useState, useEffect } from "react";
import gear from "./icons/gear.png";
import Filter from "bad-words";

const filter = new Filter();
filter.removeWords("rape", "abuse");

const kbTop = ["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"];
const kbMid = ["A", "S", "D", "F", "G", "H", "J", "K", "L"];
const kbBot = ["ENTER", "Z", "X", "C", "V", "B", "N", "M", "DEL"];

function App() {
  const [maxAttempts, setmaxAttempts] = useState(5);
  const [puzzleLength, setPuzzleLength] = useState(5);
  const [currentAttempt, setCurrentAttempt] = useState(0);
  const [attemptsArray, setAttemptsArray] = useState(
    Array(maxAttempts).fill(Array(puzzleLength).fill(" "))
  );
  const [gameCount, setGameCount] = useState(0);
  const [wordsFetched, setWordsFetched] = useState(0);
  const [word, setWord] = useState([]);
  const [success, setSuccess] = useState(false);
  const [complete, setComplete] = useState(false);
  const [impossible, setImpossible] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isInvalid, setIsInvalid] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [puzzleLengthChanged, setPuzzleLengthChanged] = useState(false);

  const handleShowSettings = () => {
    setShowSettings((prev) => !prev);
  };

  const handleAnswerInput = (e) => {
    const value = e.target.children[0].innerText;
    const current = attemptsArray[currentAttempt];
    const index = current.indexOf(" ");
    if (index === -1) return;
    setAttemptsArray((prev) => {
      const newArray = Array.from(prev.map((val) => val.map((arr) => arr)));
      newArray[currentAttempt][index] = value;
      return newArray;
    });
  };

  const handleDelete = () => {
    const current = attemptsArray[currentAttempt];
    let index = current.indexOf(" ");
    if (index === 0) return;
    if (index === -1) {
      index = puzzleLength;
    }
    setAttemptsArray((prev) => {
      const newArray = Array.from(prev.map((val) => val.map((arr) => arr)));
      newArray[currentAttempt][index - 1] = " ";
      return newArray;
    });
  };

  const handleAnswerSubmit = async () => {
    setIsLoading(true);
    let wordCheck = await checkWord();
    console.log(wordCheck);
    if (!wordCheck) {
      setIsLoading(false);
      setIsInvalid(true);
      setTimeout(() => setIsInvalid(false), 1500);
      return;
    }
    setIsLoading(false);
    const current = attemptsArray[currentAttempt];
    current.forEach((value) => {
      if (!word.includes(value) && !impossible.includes(value)) {
        setImpossible((prev) => [...prev, value]);
      }
    });
    if (attemptsArray[currentAttempt].join("") === word.join("")) {
      setAttemptsArray((prev) => {
        return Array.from(prev.filter((_, index) => index === currentAttempt));
      });
      setSuccess(true);
      setComplete(true);
    } else {
      if (currentAttempt === maxAttempts - 1) {
        setComplete(true);
      }
    }
    setCurrentAttempt((prev) => {
      return prev + 1;
    });
  };

  const handleChangeWordLength = (e) => {
    const increment = e.target.innerText === "+" ? 1 : -1;
    if (
      (increment === 1 && puzzleLength > 6) ||
      (increment === -1 && puzzleLength < 5)
    ) {
      return;
    }
    setPuzzleLength((prev) => prev + increment);
    setPuzzleLengthChanged(true);
  };

  const checkWord = async () => {
    const inputWord = attemptsArray[currentAttempt].join("").toLowerCase();
    const url = `https://api.datamuse.com/words?sp=${inputWord}`;
    let valid = false;
    await (async () => {
      const response = await fetch(url);
      const res = await response.json();
      for (let i = 0; i < res.length; i++) {
        if (res[i].word === inputWord) {
          valid = true;
          console.log("true result from check");
          break;
        }
      }
    })();
    return valid;
  };

  const fetchWord = async () => {
    let alpha = [
      "a",
      "b",
      "c",
      "d",
      "e",
      "f",
      "g",
      "h",
      "i",
      "j",
      "k",
      "l",
      "m",
      "n",
      "o",
      "p",
      "q",
      "r",
      "s",
      "t",
      "u",
      "v",
      "w",
      "y",
      "z",
    ];
    let randomLetter = alpha[Math.floor(Math.random() * alpha.length)];
    let word = "";
    const url = `https://api.datamuse.com/words?sp=${randomLetter}${"?".repeat(
      puzzleLength - 1
    )}&max=300&md=f`;
    console.log(url);
    try {
      let response = await fetch(url);
      let res = await response.json();
      res = res.filter(
        (val) =>
          !filter.clean(val.word).match(/[*]/) &&
          +val.tags[0].replace("f:", "") > 10 &&
          !val.word.match(" ") &&
          !val.word.match(/[!"£$%^&*?-_0-9]/)
      );
      if (res.length === 0) {
        return await fetchWord();
      }
      console.log("res", res);
      word = res[Math.floor(Math.random() * res.length)].word;
    } catch (err) {
      if (err) console.log(err);
    }

    return word.toUpperCase();
  };

  const handleReset = () => {
    setAttemptsArray(Array(maxAttempts).fill(Array(puzzleLength).fill(" ")));
    setCurrentAttempt(0);
    setImpossible([]);
    setComplete(false);
    setSuccess(false);
    setGameCount((prev) => prev + 1);
  };

  useEffect(() => {
    if (wordsFetched === gameCount) {
      (async () => {
        const newWord = await fetchWord();
        console.log("new", newWord);
        setWord(newWord.split(""));
        setWordsFetched((prev) => prev + 1);
      })();
    }
    if (puzzleLengthChanged) {
      handleReset();
      setPuzzleLengthChanged(false);
    }
  }, [wordsFetched, gameCount, puzzleLengthChanged]);

  const isCorrect = (letter, index) => {
    return letter === word[index];
  };

  const isPresent = (wholeWord, i) => {
    const letter = wholeWord[i];
    const otherCorrect = wholeWord.filter(
      (val, index) => index !== i && val === letter && val === word[index]
    ).length;
    const alreadyUsed = wholeWord.filter(
      (val, index) => val === letter && index < i && val !== word[index]
    ).length;
    return (
      word.filter((val) => val === letter).length - alreadyUsed - otherCorrect >
      0
    );
  };

  const attemptsLines = Array(attemptsArray.length)
    .fill()
    .map((_, index) => (
      <div
        key={index}
        className={`attempt-line ${index === currentAttempt && "current"} ${
          index < currentAttempt && "shrink"
        }`}
        style={{ display: index > currentAttempt ? "none" : "flex" }}
      >
        {Array(puzzleLength)
          .fill()
          .map((_, i) => (
            <div
              className={`letter-square no-select ${
                currentAttempt > index && isCorrect(attemptsArray[index][i], i)
                  ? "correct"
                  : currentAttempt > index && isPresent(attemptsArray[index], i)
                  ? "present"
                  : "false"
              } ${index === currentAttempt && isInvalid && "shake"}`}
              key={i}
            >
              <p className="letter">{attemptsArray[index][i]}</p>
            </div>
          ))}
      </div>
    ));

  const completeMsg = success
    ? "Well done! You found the word!"
    : `Unlucky! The word was ${word.join("")}. Better luck next time!`;

  return (
    <div className="App">
      <div
        className="background-modal"
        onClick={handleShowSettings}
        style={{ display: showSettings ? "block" : "none" }}
      />
      <img onClick={handleShowSettings} src={gear} className="gear-icon" />
      <div
        className="settings-menu"
        style={{ display: showSettings ? "block" : "none" }}
      >
        <div className="word-length-wrapper">
          <p>Word length</p>
          <div className="word-length">{puzzleLength}</div>
          <div className="change-word-length-wrapper">
            <button
              className="change-word-length-btn"
              onClick={handleChangeWordLength}
            >
              –
            </button>
            <button
              className="change-word-length-btn"
              onClick={handleChangeWordLength}
            >
              +
            </button>
          </div>
        </div>
        <p className="settings-msg">
          Changing this setting will reset the word
        </p>
      </div>
      <div className="title no-select">Lexical</div>
      <div className="invalid" style={{ display: isInvalid ? "flex" : "none" }}>
        Word is not valid
      </div>
      <div className="game-section">{attemptsLines}</div>
      <div className="input">
        <div className="kb-row">
          {kbTop.map((val, index) => {
            return (
              <button
                className="key"
                key={index}
                disabled={impossible.includes(val)}
                onClick={handleAnswerInput}
              >
                <p className="key-text no-select">{val}</p>
              </button>
            );
          })}
        </div>
        <div className="kb-row">
          {kbMid.map((val, index) => {
            return (
              <button
                className="key"
                key={index}
                disabled={impossible.includes(val)}
                onClick={handleAnswerInput}
              >
                <p className="key-text no-select">{val}</p>
              </button>
            );
          })}
        </div>
        <div className="kb-row">
          {kbBot.map((val, index) => {
            return (
              <button
                className={`key ${val}`}
                key={index}
                disabled={
                  impossible.includes(val) ||
                  (val === "ENTER" &&
                    attemptsArray[currentAttempt] &&
                    attemptsArray[currentAttempt].includes(" ")) ||
                  (val === "DEL" &&
                    attemptsArray[currentAttempt] &&
                    attemptsArray[currentAttempt][0] === " ")
                }
                onClick={
                  val === "DEL"
                    ? handleDelete
                    : val === "ENTER"
                    ? handleAnswerSubmit
                    : handleAnswerInput
                }
              >
                <p className="key-text no-select">{val}</p>
              </button>
            );
          })}
        </div>
      </div>
      {complete && (
        <div className="complete-modal">
          <p className="complete-msg">{completeMsg}</p>
          <button onClick={handleReset} className="complete-btn">
            play again
          </button>
        </div>
      )}
    </div>
  );
}

export default App;
